import React from "react"
import Logo from "../img/Logo.png"
import RibuBiggerLogo from "../img/ribu-bigger-logo.png"


export default function WhyRibu() {

    return (

        <section className="why-ribu">
            <div className="title">

            </div>
            <div className="why-ribu-container">

                <div className="why-ribu-title">
                    <div className="ribu-bigger-logo"><img src={RibuBiggerLogo} alt="ribu logo"/></div>
                    <div className="why-ribu-slogan">Neden Ribu?</div>
                </div>

                <div className="cards-area">
                    <div className="column">
                        <div className="card">
                            <div className="card-title">Eitir</div>
                            <div className="card-description">Kullanclara zel hazrlanm trading eitimleri ile trade konseptlerini retir.</div>
                        </div>
                        <div className="card">
                            <div className="card-title">Test Eder</div>
                            <div className="card-description">Hazrlanm olan challenge ve quizler ile eitim alan kullanclarn kendini test etmesine olanak salar.</div>
                        </div>
                    </div>

                    <div className="column">
                        <div className="card">
                            <div className="card-title">Fonlar</div>
                            <div className="card-description">Challengelar ve quizleri baaryla tamamlayan kullanclar fonlayarak yksek bakiyeli hesaplar ynetmelerine olanak tanr.</div>
                        </div>
                        <div className="card">
                            <div className="card-title">Kazandrr</div>
                            <div className="card-description">Traderlar ynettikleri fonlar zerinden elde ettii karn %75'ini direkt olarak ekebilir.</div>
                        </div>
                    </div>

                </div>

            </div>
        </section>

    )
}